import './reset.scss';

const myEnergy = {
    energy : 0,
    energyPerClick: 1
    }
    
function collectEnergy() {
    // myEnergy.energy += myEnergy.energyPerClick;
    myEnergy.energy = myEnergy.energy + myEnergy.energyPerClick;

    document.getElementById("collectedEnergy").innerHTML = "collectedEnergy : " + myEnergy.energy;
}